.gallery {
  padding: 2rem 5rem;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: auto;
  justify-self: stretch;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  justify-items: center; }
  .gallery h2 {
    grid-column-start: 1;
    grid-column-end: 4; }
  .gallery div {
    overflow: hidden;
    min-width: 80%;
    max-width: 100%;
    position: relative;
    overflow: hidden; }
    .gallery div img {
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      border: 5px solid transparent;
      max-width: 100%;
      -o-object-fit: contain;
      object-fit: contain; }
  .gallery div:nth-child(2) {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 4; }
  .gallery div:nth-child(7) {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 8;
    grid-row-end: 11; }
  .gallery div:nth-child(8) {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 4;
    grid-row-end: 6; }
  .gallery div:nth-child(10) {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 5;
    grid-row-end: 7; }
  .gallery div:hover img,
  .gallery div:active img {
    border: 5px solid #a30606;
    -webkit-transition: 1s ease-in-out;
    transition: 1s ease-in-out; }

@media screen and (max-width: 900px) {
  .gallery {
    padding: 2rem;
    grid-template-columns: 1fr 1fr; }
    .gallery h2 {
      grid-column-end: 3; }
    .gallery div:nth-child(2),
    .gallery div:nth-child(10) {
      grid-row-start: auto;
      grid-row-end: auto;
      grid-column-start: auto;
      grid-column-end: auto; }
    .gallery div:nth-child(8) {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 3;
      grid-row-end: 5; }
    .gallery div:nth-child(7) {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 8;
      grid-row-end: 10; } }

@media screen and (max-width: 550px) {
  .gallery {
    padding: 1rem;
    grid-template-columns: 1fr; }
    .gallery h2 {
      grid-column-end: auto; }
    .gallery div:nth-child(7),
    .gallery div:nth-child(8) {
      grid-row-start: auto;
      grid-row-end: auto;
      grid-column-start: auto;
      grid-column-end: auto; } }
